import React from 'react';
// import Img from "gatsby-image";
import photo from './erich.jpg';
import './_welcome.scss';

const NavBar = (props) => (
    <div className="section-wrap">
        <div className="square">
            <div className="content">
                <h1>
                    <small className="h-label">Hello, my name is</small>
                    <strong className="name">Erich González</strong>
                </h1>
                <h2 className="description">
                    <span>I'm an enthusiastic front-end developer,</span>
                    <small className='d-block'>with 4+ years experience.</small>
                </h2>
            </div>
        </div>
        <div className="photo" style={{backgroundImage: `url(${photo})`}}/>
    </div>
);

export default NavBar;


